"use client";

import { useState, useEffect } from "react";
import { Product } from "../types/product";
import { ProductStorage } from "../lib/storage";
import { ProductForm } from "../components/product-form-dynamic";
import { ImageUpload } from "../components/image-upload";
import { InventoryDashboard } from "../components/inventory-dashboard-final";

export default function Home() {
  const [products, setProducts] = useState<Product[]>([]);

  useEffect(() => {
    const loadProducts = () => {
      const storedProducts = ProductStorage.getProducts();
      if (storedProducts.length === 0) {
        // Initialize with sample data
        const sampleProducts: Product[] = [
          {
            id: "1",
            name: "Wireless Earbuds Pro",
            sku: "WEP-001",
            marketplace: "amazon",
            category: "Electronics",
            price: 2999,
            costPrice: 1800,
            stock: 45,
            minStock: 10,
            maxStock: 100,
            description: "Premium wireless earbuds with noise cancellation",
            images: [],
            supplier: "TechSupplier Co.",
            weight: 50,
            dimensions: { length: 5, width: 3, height: 2 },
            isActive: true,
            createdAt: new Date(),
            updatedAt: new Date(),
          },
          {
            id: "2",
            name: "Smart Watch Ultra",
            sku: "SWU-002",
            marketplace: "flipkart",
            category: "Electronics",
            price: 15999,
            costPrice: 12000,
            stock: 12,
            minStock: 5,
            maxStock: 50,
            description: "Advanced smartwatch with health monitoring",
            images: [],
            supplier: "GadgetWorld",
            weight: 80,
            dimensions: { length: 4, width: 4, height: 1 },
            isActive: true,
            createdAt: new Date(),
            updatedAt: new Date(),
          },
        ];
        ProductStorage.saveProducts(sampleProducts);
        setProducts(sampleProducts);
      } else {
        setProducts(storedProducts);
      }
    };

    loadProducts();
  }, []);

  const handleProductUpdate = () => {
    setProducts(ProductStorage.getProducts());
  };

  return (
    <main className="min-h-screen bg-gray-50">
      <div className="container mx-auto px-4 py-8">
        <InventoryDashboard products={products} onProductUpdate={handleProductUpdate} />
      </div>
    </main>
  );
}
